import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import { LocationProvider } from "./library/LocationContext";

// Importing from react-router-dom
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import ErrorPage from "./pages/ErrorPage";
import TemperatureGraph from "./pages/TemperatureGraph";
import LocationPermission from "./components/LocationPermission";

const router = createBrowserRouter([
  {
    path: "/ecs522u-project",
    element: <LocationPermission />,
    errorElement: <LocationPermission />,
  },
  {
    path: "/home",
    element: <App />,
    errorElement: <ErrorPage />,
  },
  {
    path: "/graphs",
    element: <TemperatureGraph />,
    errorElement: <ErrorPage />,
  },
  {
    path: "/graphs/temperature",
    element: <TemperatureGraph />,
    errorElement: <ErrorPage />,
  },
]);
const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <LocationProvider>
    <React.StrictMode>
      <RouterProvider router={router} />{" "}
    </React.StrictMode>
  </LocationProvider>
);
