import React, { useContext, useState } from "react";
import { Link } from "react-router-dom";
import "boxicons";
import LocationContext from "../library/LocationContext";
import LogoX from "../assets/logos/horizontal_logo.png";

const Header = () => {
  const { setLocation } = useContext(LocationContext);
  const [menuOpen, setMenuOpen] = useState(false);
  const [searchValue, setSearchValue] = useState("");

  const handleLocationSearch = () => {
    setLocation(searchValue);
    setMenuOpen(false); // Close the menu after searching
    setSearchValue(""); // Clear the search input after searching
  };

  return (
    <div className="py-2 md:py-8 px-4 md:px-16 flex flex-col items-center md:items-end">
      <div className="flex flex-col md:flex-row items-center justify-normal md:justify-between w-full">
        <Link to={"/"}>
          <img
            src={LogoX}
            alt="SiteShield Logo"
            className="w-[14rem] md:w-[16rem] h-auto"
          />
        </Link>
        <header className="w-full md:w-auto flex items-center gap-8">
          <input
            type="text"
            placeholder="Enter Location"
            value={searchValue}
            onChange={(e) => setSearchValue(e.target.value)}
            className="w-full lg:w-[450px] md:w-[400px] border-2 border-black px-4 py-2 rounded-full text-textBlack placeholder:text-neutral-700 font-Poppins bg-[#f8f8ff]"
          />
          <button onClick={handleLocationSearch}>
            <box-icon name="search-alt" size="40px" color="#191919"></box-icon>
          </button>
          <button onClick={() => setMenuOpen(!menuOpen)}>
            {menuOpen ? (
              <box-icon name="x" size="50px" color="#191919"></box-icon>
            ) : (
              <box-icon name="menu" size="50px" color="#191919"></box-icon>
            )}
          </button>
        </header>
      </div>
      {menuOpen && (
        <nav className="my-8 flex flex-col items-center md:items-end gap-6">
          <Link
            to={"/home"}
            className="font-OpenSans text-xl text-textBlack hover:text-sky-800 transition-colors duration-200"
          >
            Home
          </Link>
          <Link
            to={"/graphs"}
            className="font-OpenSans text-xl text-textBlack hover:text-sky-800 transition-colors duration-200"
          >
            Weather Graphs
          </Link>
        </nav>
      )}
    </div>
  );
};

export default Header;
