import React, { useContext, useEffect, useState } from "react";
import { getWeatherForecast } from "../library/functions";
import LocationContext from "../library/LocationContext";
import { Chart } from "react-google-charts";
import Header from "../components/Header";

const chartOptions = {
  hAxis: {
    title: "Day of the",
  },
  backgroundColor: "#F8F8FF",
  vAxis: {
    title: "Temperature (C)",
  },
};

const TemperatureGraph = () => {
  const { location } = useContext(LocationContext);
  const [weatherForecast, setWeatherForecast] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const weatherForecastData = await getWeatherForecast({ location });
        setWeatherForecast(weatherForecastData.list); // Extracting forecast data array
      } catch (error) {
        console.error("Error fetching weather data:", error);
      }
    };

    if (location) {
      fetchData();
    }
  }, [location]);

  // Function to calculate average, max, and min temperatures for each day
  const calculateDailyTemperatures = () => {
    if (!weatherForecast || !Array.isArray(weatherForecast)) return [];

    const dailyData = {};
    weatherForecast.forEach((entry) => {
      const date = new Date(entry.dt * 1000);
      const day = date.toLocaleDateString("en-US", { weekday: "short" });
      const temperature = entry.main.temp;

      if (!dailyData[day]) {
        dailyData[day] = {
          temperatures: [temperature],
          max: temperature,
          min: temperature,
        };
      } else {
        dailyData[day].temperatures.push(temperature);
        if (temperature > dailyData[day].max) dailyData[day].max = temperature;
        if (temperature < dailyData[day].min) dailyData[day].min = temperature;
      }
    });

    // Calculate average temperature for each day
    Object.keys(dailyData).forEach((day) => {
      const temperatures = dailyData[day].temperatures;
      const sum = temperatures.reduce((acc, curr) => acc + curr, 0);
      dailyData[day].average = sum / temperatures.length;
    });

    return dailyData;
  };

  // Prepare data for the chart
  let chartData = [
    ["Day", "Average Temperature", "Max Temperature", "Min Temperature"],
  ];
  const dailyTemperatures = calculateDailyTemperatures();
  Object.keys(dailyTemperatures).forEach((day) => {
    const { average, max, min } = dailyTemperatures[day];
    chartData.push([day, average, max, min]);
  });

  // Render only if weatherForecast has data
  const renderChart = weatherForecast && weatherForecast.length > 0;

  return (
    <main>
      <Header />
      {renderChart && (
        <>
          <h1 className="font-Ubuntu text-3xl text-center">
            Temperature for this week
          </h1>
          <div className="font-Ubuntu">
            <Chart
              width={"100%"}
              height={"400px"}
              chartType="LineChart"
              loader={<div>Loading Chart</div>}
              data={chartData}
              options={chartOptions}
            />
          </div>
        </>
      )}
    </main>
  );
};

export default TemperatureGraph;
