import axios from "axios";

// Function to get todays date in the correct format
function getCurrentDate() {
  const currentDate = new Date();
  const options = {
    weekday: "long",
    day: "numeric",
    month: "long",
    year: "numeric",
  };
  const formattedDate = currentDate.toLocaleDateString("en-UK", options);
  return formattedDate;
}

const getCurrenthour = (offset) => {
  let currentHour = new Date().getHours();
  const nextHour = (currentHour + offset) % 24;
  return `${nextHour}:00`;
};

function unixTimestampToHHMM(unixTimestamp) {
  const milliseconds = unixTimestamp * 1000;
  const dateObject = new Date(milliseconds);
  const hours = dateObject.getHours().toString().padStart(2, "0");
  const minutes = dateObject.getMinutes().toString().padStart(2, "0");
  const formattedTime = `${hours}:${minutes}`;
  return formattedTime;
}

const getCurrentWeather = async ({location}) => {
  try {
    const response = await axios.get(
      `https://api.openweathermap.org/data/2.5/weather?q=${location}&units=metric&appid=968075323563e71f4e993efdd32709d2`
    );
    if (response) {
      return response.data;
    }
  } catch (e) {
    console.log("An Error occurred:", e);
  }
};

const getWeatherForecast = async ({location}) => {
  try {
    const response = await axios.get(
      `https://api.openweathermap.org/data/2.5/forecast?q=${location}&appid=968075323563e71f4e993efdd32709d2&units=metric`
    );
    if (response) {
      return response.data;
    }
  } catch (e) {
    console.log("An Error occurred:", e);
  }
};

export {
  getCurrentDate,
  getCurrenthour,
  unixTimestampToHHMM,
  getCurrentWeather,
  getWeatherForecast,
};
