import React from "react";
import { motion } from "framer-motion";
import {
  Icon01n,
  Icon02n,
  Icon03n,
  Icon04n,
  Icon09n,
  Icon10n,
  Icon11n,
  Icon13n,
  Icon50n,
  Icon01d,
  Icon02d,
  Icon03d,
  Icon04d,
  Icon09d,
  Icon10d,
  Icon11d,
  Icon13d,
  Icon50d,
} from "../assets/weather_icons/";

const CurrentWeather = ({
  iconNumber,
  location,
  temp,
  high,
  low,
  feelsLike,
}) => {
  let weatherIcon;
  switch (iconNumber) {
    case "01n":
      weatherIcon = Icon01n;
      break;
    case "02n":
      weatherIcon = Icon02n;
      break;
    case "03n":
      weatherIcon = Icon03n;
      break;
    case "04n":
      weatherIcon = Icon04n;
      break;
    case "09n":
      weatherIcon = Icon09n;
      break;
    case "10n":
      weatherIcon = Icon10n;
      break;
    case "11n":
      weatherIcon = Icon11n;
      break;
    case "13n":
      weatherIcon = Icon13n;
      break;
    case "50n":
      weatherIcon = Icon50n;
      break;
    case "01d":
      weatherIcon = Icon01d;
      break;
    case "02d":
      weatherIcon = Icon02d;
      break;
    case "03d":
      weatherIcon = Icon03d;
      break;
    case "04d":
      weatherIcon = Icon04d;
      break;
    case "09d":
      weatherIcon = Icon09d;
      break;
    case "10d":
      weatherIcon = Icon10d;
      break;
    case "11d":
      weatherIcon = Icon11d;
      break;
    case "13d":
      weatherIcon = Icon13d;
      break;
    case "50d":
      weatherIcon = Icon50d;
      break;
    default:
      weatherIcon = null;
  }

  // Determine background color based on temperature
  let bgColorClass;
  if (temp < 10) {
    bgColorClass = "bg-cold";
  } else if (temp > 23) {
    bgColorClass = "bg-hot";
  } else {
    bgColorClass = "bg-warm";
  }

  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.5 }}
      animate={{ opacity: 1, scale: 1 }}
      transition={{ duration: 0.5 }}
      className={`w-[300px] h-[300px] md:w-[500px] md:h-[500px] border ${bgColorClass} p-5 m-4 rounded-3xl flex flex-col items-center justify-center gap-4`}
    >
      {weatherIcon && (
        <motion.img
          initial={{ y: -50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.2, duration: 0.5 }}
          src={weatherIcon}
          alt="weather condition"
          className="w-20 sm:w-24 drop-shadow-xl"
        />
      )}
      <motion.p
        initial={{ y: -20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ delay: 0.4, duration: 0.5 }}
        className="font-Ubuntu text-matteBlack text-3xl sm:text-4xl"
      >
        {location}
      </motion.p>
      <motion.p
        initial={{ y: -20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ delay: 0.6, duration: 0.5 }}
        className="font-Poppins text-matteBlack text-4xl sm:text-5xl"
      >
        {temp}C
      </motion.p>
      <motion.p
        initial={{ y: -20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ delay: 0.8, duration: 0.5 }}
        className="font-Poppins text-matteBlack text-md sm:text-lg"
      >
        High: {high}C | Low: {low}C
      </motion.p>
      <motion.p
        initial={{ y: -20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ delay: 1, duration: 0.5 }}
        className="font-Poppins text-matteBlack text-md"
      >
        Feels Like: {feelsLike}C
      </motion.p>
    </motion.div>
  );
};

export default CurrentWeather;
