import React from "react";

const LocationPermission = () => {
  const handleAllow = () => {
    // Show confirmation alert
    const allow = window.confirm("Allow SiteShield to access your location?");
    if (allow) {
      // If user confirms, route to '/home'
      window.location.href = "/ecs522u-project/home";
    }
  };

  const handleNotAllow = () => {
    // Show confirmation alert
    const notAllow = window.confirm(
      "Are you sure you want to not allow access to your location?"
    );
    if (notAllow) {
      // If user confirms, route to '/'
      window.location.href = "/ecs522u-project";
    }
  };

  return (
    <main className="flex items-center justify-center h-screen text-center">
      <div className="w-full">
        <div>
          <h1 className="font-Ubuntu text-3xl my-2">
            Allow SiteShield to access your location?
          </h1>
          <div className="flex flex-col items-center w-full gap-4">
            <button
              onClick={handleAllow}
              className="w-[300px] h-[50px] bg-buttonBlue rounded-full text-white hover:scale-110 transition-all duration-200"
            >
              Allow
            </button>
            <button
              onClick={handleNotAllow}
              className="w-[300px] h-[50px] bg-buttonBlue rounded-full text-white hover:scale-110 transition-all duration-200"
            >
              Don't Allow
            </button>
          </div>
        </div>
      </div>
    </main>
  );
};

export default LocationPermission;
