import Accordion from "./Accordion";
import React, { useContext, useEffect, useState } from "react";

import LocationContext from "../library/LocationContext";
import {
  getCurrentWeather,
  getWeatherForecast,
  unixTimestampToHHMM,
} from "../library/functions";

const WeatherDetails = () => {
  const { location } = useContext(LocationContext);
  const [currentWeather, setCurrentWeather] = useState(null);
  const [forecast, setForecast] = useState({
    forecast9am: null,
    forecast3pm: null,
    forecast6pm: null,
  });

  useEffect(() => {
    const fetchWeather = async () => {
      try {
        const weatherResponse = await getCurrentWeather({ location });
        setCurrentWeather(weatherResponse);
      } catch (error) {
        console.log("Error:", error);
      }
    };

    if (location) {
      fetchWeather();
    }
  }, [location]);

  useEffect(() => {
    const fetchForecast = async () => {
      try {
        const forecastResponse = await getWeatherForecast({ location });
        const forecastData = forecastResponse.list;

        let forecast9am = null;
        let forecast3pm = null;
        let forecast6pm = null;

        forecastData.forEach((item) => {
          const forecastTime = new Date(item.dt * 1000).getHours();

          if (forecastTime === 9 && forecast9am === null) {
            forecast9am = item;
          }

          if (forecastTime === 15 && forecast3pm === null) {
            forecast3pm = item;
          }

          if (forecastTime === 18 && forecast6pm === null) {
            forecast6pm = item;
          }
        });

        setForecast({
          forecast9am,
          forecast3pm,
          forecast6pm,
        });
      } catch (error) {
        console.log("Error fetching forecast:", error);
      }
    };

    if (location) {
      fetchForecast();
    }
  }, [location]);

  return (
    <div className="flex items-baseline flex-wrap justify-center">
      {currentWeather &&
        forecast.forecast9am &&
        forecast.forecast3pm &&
        forecast.forecast6pm && (
          <div className="flex flex-col md:flex-row w-full">
            <Accordion
              accordionTitle={"Pressure"}
              accordionValue={`${currentWeather.main.pressure} hPa`}
              accordionIconName={"pressure"}
              hourlyValues={[
                currentWeather.main.pressure,
                forecast.forecast9am.main.pressure,
                forecast.forecast3pm.main.pressure,
                forecast.forecast6pm.main.pressure,
              ]}
            />
            <Accordion
              accordionTitle={"Humidity"}
              accordionValue={`${currentWeather.main.humidity} %`}
              accordionIconName={"humidity"}
              hourlyValues={[
                currentWeather.main.humidity,
                forecast.forecast9am.main.humidity,
                forecast.forecast3pm.main.humidity,
                forecast.forecast6pm.main.humidity,
              ]}
            />
            <Accordion
              accordionTitle={"Wind"}
              accordionValue={`${currentWeather.wind.speed} m/s @ ${currentWeather.wind.deg}`}
              accordionIconName={"wind"}
              hourlyValues={[
                currentWeather.wind.speed,
                forecast.forecast9am.wind.speed,
                forecast.forecast3pm.wind.speed,
                forecast.forecast6pm.wind.speed,
              ]}
            />
          </div>
        )}
      <div className="flex sm:flex-row flex-col items-center w-full justify-center">
        {currentWeather &&
          forecast.forecast9am &&
          forecast.forecast3pm &&
          forecast.forecast6pm && (
            <>
              <Accordion
                accordionTitle={"Sunrise"}
                accordionValue={`${unixTimestampToHHMM(
                  currentWeather.sys.sunrise
                )} `}
                accordionIconName={"sunrise"}
                accordionType={"sundial"}
                hourlyValues={[
                  currentWeather.sys.sunrise,
                  forecast.forecast9am.sys.sunrise,
                  forecast.forecast3pm.sys.sunrise,
                  forecast.forecast6pm.sys.sunrise,
                ]}
              />
              <Accordion
                accordionTitle={"Sunset"}
                accordionValue={`${unixTimestampToHHMM(
                  currentWeather.sys.sunset
                )}`}
                accordionIconName={"sunset"}
                accordionType={"sundial"}
                hourlyValues={[
                  currentWeather.sys.sunset,
                  forecast.forecast9am.sys.sunset,
                  forecast.forecast3pm.sys.sunset,
                  forecast.forecast6pm.sys.sunset,
                ]}
              />
            </>
          )}
      </div>
    </div>
  );
};

export default WeatherDetails;
