import React, { useContext, useEffect, useState } from "react";
import CurrentWeather from "../components/CurrentWeather";
import Header from "../components/Header";
import axios from "axios";
import LocationContext from "../library/LocationContext";
import { getCurrentDate } from "../library/functions";
import WeatherDetails from "../components/WeatherDetails";
import Footer from "../components/Footer";

const HomePage = () => {
  const { location } = useContext(LocationContext);
  const [currentWeather, setCurrentWeather] = useState({});

  useEffect(() => {
    const fetchWeather = async () => {
      try {
        const weatherResponse = await axios.get(
          `https://api.openweathermap.org/data/2.5/weather?q=${location}&units=metric&appid=968075323563e71f4e993efdd32709d2`
        );
        setCurrentWeather(weatherResponse.data);
      } catch (error) {
        console.log("Error:", error);
      }
    };

    if (location) {
      fetchWeather();
    }
  }, [location]);
  const Hero = () => {
    return (
      <div className="flex flex-col md:flex-row items-center md:items-baseline justify-center md:justify-between gap-4 py-4 md:py-8 px-4 md:px-16  mx-10 rounded-xl">
        {currentWeather &&
          currentWeather.weather &&
          currentWeather.weather.length > 0 && (
            <CurrentWeather
              iconNumber={currentWeather.weather[0].icon}
              location={currentWeather.name}
              temp={Math.round(currentWeather.main.temp)}
              high={Math.round(currentWeather.main.temp_max)}
              low={Math.round(currentWeather.main.temp_min)}
              feelsLike={Math.round(currentWeather.main.feels_like)}
            />
          )}
        <div className="text-center md:text-right">
          {currentWeather.weather && (
            <p className="uppercase text-2xl font-Nunito">
              {currentWeather.weather[0].description}
            </p>
          )}
          <p className="font-Ubuntu text-2xl md:text-3xl my-4">
            {getCurrentDate()}
          </p>

          {currentWeather.weather &&
            currentWeather.weather[0].main === "Rain" && (
              <p className="font-Poppins text-2xl md:text-3xl flex items-center gap-3 hidden sm:block">
                <i class="bx bx-bulb text-5xl text-yellow-400"></i>
                Ensure you cover all building materials
              </p>
            )}
          {currentWeather.weather &&
            currentWeather.weather[0].main === "Tornado" && (
              <p className="font-Poppins text-2xl md:text-3xl flex items-center gap-3 hidden sm:block">
                <i className="bx bx-bulb text-5xl text-yellow-400"></i>
                Do not work in these conditions
              </p>
            )}
          {currentWeather.weather &&
            currentWeather.weather[0].main === "Fog" && (
              <p className="font-Poppins text-2xl md:text-3xl flex items-center gap-3 hidden sm:block">
                <i className="bx bx-bulb text-5xl text-yellow-400"></i>
                Ensure low visibility measures are in place.
              </p>
            )}
          {currentWeather.weather &&
            currentWeather.weather[0].main === "Clear" && (
              <p className="font-Poppins text-2xl md:text-3xl flex items-center gap-3 hidden sm:block">
                <i className="bx bx-bulb text-5xl text-yellow-400 "></i>
                Should be a great day for some construction.
              </p>
            )}
          {currentWeather.weather &&
            currentWeather.weather[0].main === "Clouds" && (
              <p className="font-Poppins text-2xl md:text-3xl flex items-center gap-3 hidden sm:block">
                <i className="bx bx-bulb text-5xl text-yellow-400"></i>
                Chance for some wetter conditions, take care.
              </p>
            )}
          {currentWeather.weather &&
            currentWeather.weather[0].main === "Smoke" && (
              <p className="font-Poppins text-2xl md:text-3xl flex items-center gap-3 hidden sm:block">
                <i className="bx bx-bulb text-5xl text-yellow-400"></i>
                Take care with open electrical works
              </p>
            )}
          {currentWeather.weather &&
            currentWeather.weather[0].main === "Thunderstorm" && (
              <p className="font-Poppins text-2xl md:text-3xl flex items-center gap-3 hidden sm:block">
                <i className="bx bx-bulb text-5xl text-yellow-400"></i>
                Ensure electricals are secure.
              </p>
            )}
        </div>
      </div>
    );
  };

  console.log(currentWeather);

  return (
    <main className="flex-col items-center gap-10">
      <Header />
      {!location && (
        <h1 className="md:text-5xl sm:text-3xl font-Nunito text-center">
          Search for a location to get started...
        </h1>
      )}
      {location && (
        <div>
          <Hero />
          <p className="text-center font-Ubuntu text-2xl md:text-4xl my-5 p-4">
            Weather Details for Today
          </p>
          <WeatherDetails />
        </div>
      )}
      <Footer />
    </main>
  );
};

export default HomePage;
