import React, { useState } from "react";
import { motion } from "framer-motion";
import {
  Sunrise,
  Sunset,
  Wind,
  WindDirection,
  Pressure,
  Humidity,
} from "../assets/accordionIcons";

const Accordion = ({
  accordionTitle,
  accordionValue,
  accordionType,
  accordionIconName,
  hourlyValues,
}) => {
  const [accordionOpen, setAccordionOpen] = useState(false);

  let accordionIcon;
  switch (accordionIconName) {
    case "sunrise":
      accordionIcon = Sunrise;
      break;
    case "sunset":
      accordionIcon = Sunset;
      break;
    case "pressure":
      accordionIcon = Pressure;
      break;
    case "wind":
      accordionIcon = Wind;
      break;
    case "windDeg":
      accordionIcon = WindDirection;
      break;
    case "humidity":
      accordionIcon = Humidity;
      break;
    default:
      accordionIcon = null;
  }

  const hours = ["9am", "2pm", "6pm"];

  return (
    <motion.div
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className={`w-full h-max flex flex-col items-center shadow-lg gap-4 py-4 px-10 m-4 rounded-2xl hover:cursor-pointer ${
        accordionType === "sundial"
          ? "bg-sunriseSunsetPurple text-white hover:cursor-auto"
          : "bg-white"
      }`}
      onClick={() => setAccordionOpen(!accordionOpen)}
    >
      <div className="w-full flex items-center justify-between">
        <motion.img
          initial={{ opacity: 0, scale: 0.5 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.5 }}
          src={accordionIcon}
          alt={accordionIcon}
          className="w-12"
        />
        <motion.div
          initial={{ opacity: 0, x: -10 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.2, duration: 0.5 }}
          className="flex flex-col items-end"
        >
          <p
            className={`font-Nunito text-xl md:text-2xl ${
              accordionType === "sundial" ? "text-white" : "text-black"
            }`}
          >
            {accordionTitle}
          </p>
          <p
            className={`font-Nunito text-lg md:text-xl ${
              accordionType === "sundial" ? "text-white" : "text-black"
            }`}
          >
            {accordionValue}
          </p>
        </motion.div>
      </div>
      {accordionOpen && accordionType !== "sundial" && (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="w-full flex flex-col"
        >
          <p className="font-OpenSans text-center">Today's Overview</p>
          <div className="w-full flex flex-col items-center">
            {hours.map((hour, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: index * 0.1 + 0.2, duration: 0.5 }}
                className="w-full flex items-center justify-between"
              >
                <p className="font-Nunito text-md md:text-lg text-black">
                  {hour}
                </p>
                <p className="font-Nunito text-md md:text-lg text-black">
                  {hourlyValues[index]}
                </p>
              </motion.div>
            ))}
          </div>
        </motion.div>
      )}
    </motion.div>
  );
};

export default Accordion;
